import React, { Component } from "react";
import Masonry from 'react-masonry-component';
import Card from "./Elements/Card";
import { connect } from 'react-redux';
import { managePostHeight } from '../../../../actions/themeActions';
import "./scss/Pixel.scss";
import ScrollAnimation from "../../../Components/ScrollAnimation";
class SquarePhoto extends Component {
  state = {
    postData: []
  }

  componentDidMount() {
    const { heightUpdate } = this.props;
    setTimeout(() => heightUpdate(), 1000)
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.postData !== this.props.postData) return true
    else return false
  }


  render() {
    const { postData, completeDataObject, adjustWidth, wall, clickToShowPopUp, cardNumber, onClickToCTA, postSizeInPx, onClickPopUpSlider } = this.props;
    let totalPostBestFit = postData.length;
    if (wall && wall.Personalization.fitRow == 1) {
      if (postData.length >= parseInt(cardNumber)) {
        let bestFitEachRow = postData.length / parseInt(cardNumber);
        totalPostBestFit = (parseInt(cardNumber) * parseInt(bestFitEachRow));
      }
    }
    return (
      <ScrollAnimation wall={wall} parentId="tb_marquee" renderID="pixel">
        <div id="tb_marquee">
          <Masonry
            id="pixel"
            className={`tb_px_post_container`}
            tabIndex='0'
            aria-label={`There are ${(postData && postData.length > 0) ? postData.length : 0} posts in the feed`}
            elementType={'div'}
            options={{
              transitionDuration: 0,
              percentPosition: true,
              itemSelector: '.tb_px_post_wrapper',
              horizontalOrder: false,
              resize: true,
            }}
            disableImagesLoaded={false}
            updateOnEachImageLoad={false}
            imagesLoadedOptions={{}}
            enableResizableChildren={true}>

            {(postData?.length > 0) && postData.map((item, index) => {
              const cardData = completeDataObject[item];
              return <Card ownerId={wall.Wall.owner} itemData={cardData} key={`sqr_crd_${index}_${cardData.id}`} itemIndex={index} adjustWidth={adjustWidth} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} wallID={wall.Wall.id} clickToShowPopUp={clickToShowPopUp} onClickToCTA={onClickToCTA} postSizeInPx={postSizeInPx} wall={wall} onClickPopUpSlider={onClickPopUpSlider} />
            })}

          </Masonry>
        </div>
      </ScrollAnimation>
    );
  }
}


const mapStateToProps = state => {
  return {
    postHeight: state.postHeight.data
  }
}

const mapDispatchToProps = dispatch => {
  return {
    managePostHeight: (data) => dispatch(managePostHeight(data))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(SquarePhoto)