import React, { Suspense, useRef, useEffect } from "react";
import { IS_THEME, PostTrimContent, themeImpressionTracking } from '../../../../../utils'
import { isSocialEmbed, isSocialWall, isTagEmbed, isTagShop, isTagbox } from "../../../../../actions/api";
import { suiteTrack } from '../../../../../actions/api'
import ShareOnHover from "../../../Elements/ShareOnHover/ShareOnHover";
import Network from "../../../Elements/Network/Network";
import ShopButton from "../../../Elements/ShopButton/ShopButton";
const AuthorDetails = React.lazy(() => import("./AuthorDetails"));
const Media = React.lazy(() => import('./Media'));
const PostContent = React.lazy(() => import('../../../Elements/PostContent/PostContent'));
const trim_content_length = (isSocialWall || isSocialEmbed) ? 50 : null;


const Card = ({ itemData, personalization, adjustWidth, ThemeRule, clickToShowPopUp, itemIndex, wallID, ownerId, postSizeInPx, wall, onClickPopUpSlider }) => {
  const postTrack = useRef(null);
  const mediaType = (itemData.type === 2 || itemData.type === 3 || itemData.type === 4 || itemData.type === 5) ? true : false;
  const postTextContent = personalization.trimcontent ? PostTrimContent(itemData.content, trim_content_length) : itemData.content
  const responsiveCardSize2x = postSizeInPx.adjustWidth < 250 ? true : false
  const themeMode = (ThemeRule.postHover == 0 && isTagShop) ? 'light' : 'dark'
  const overlay = isTagbox ? 'tb_px_color_overlay' : 'tb_px_post_overlay-' + themeMode
  const overlayStyle = { backgroundColor: isTagbox ? ThemeRule.backgroundColor : '' }
  useEffect(() => {
    if (suiteTrack) themeImpressionTracking(postTrack);
  }, []);
  const isHotspot = itemData.hotspot && itemData.ugc_products.length > 0 ? true : false
  const isShopBtn = isTagShop && (!itemData.hotspot && itemData.ugc_products.length > 0) ? true : false;
  const showAuthor = ThemeRule.postAuthor || ThemeRule.postTime ? true : false

  useEffect(() => {
    if (itemData.isPopUp) onClickPopUpSlider(itemIndex, itemData)
  }, [itemIndex, itemData.isPopUp, itemData]);
  return <div id={`tb-sp-post-${itemData.id}`} className={`tb_px_post_wrapper${responsiveCardSize2x ? ' tb_px_post_wrapper2x' : ''}`} style={{ width: `${adjustWidth}%`, padding: personalization.padding / 2 }} tb-network={itemData.network.id} tb-product={isShopBtn ? 1 : isHotspot ? 2 : 0} tabIndex="0" role="article" aria-label={`Post ${parseInt(itemIndex) + 1},${postTextContent}`} ref={postTrack} post-id={itemData.referenceId ? itemData.referenceId : itemData.id} feed-id={itemData.feedId}>
    <div className={`tb_px_post_in${isTagShop ? ' tb_px_post_ani' : ''}`} onClick={clickToShowPopUp(itemIndex, itemData)}>
      <div className="tb_px_post_media_wrapp">
        {itemData.share.status ? <ShareOnHover share={itemData.share} shareClass={'tb_px_share_container'} item={itemData} wallID={wallID} ownerId={ownerId} /> : ''}
        {mediaType ? <Suspense fallback={<></>}><Media key={`img${itemIndex}_${itemData.id}`} itemData={itemData} wallID={wallID} ownerId={ownerId} ThemeID={personalization.widgetTheme} wall={wall} /></Suspense> : ''}
        {showAuthor ?
          <Suspense fallback={<></>}><AuthorDetails key={`img${itemIndex}_${itemData.id}`} ownerId={ownerId} postTime={itemData.createdAt} author={itemData.author} network={itemData.network} font={ThemeRule} personalization={personalization} ThemeRule={ThemeRule} /></Suspense>
          : null}
      </div>
    </div>
  </div>
}
export default Card