import { Component, memo } from "react";
import { isSocialWall, isSocialEmbed } from "../../../actions/api";

var ReachedMaxScroll = false;
var PreviousScrollTop = 0;
var setTimescroll = 0;
var ScrollInterval = 0;
var scrollTime, DivElmnt;
const speedUpPixel = scrollDelay => ({ 80: 60, 70: 40, 50: 30, 30: 20, 20: 10 }[scrollDelay] ?? 20);
const scrollSpeedValue = window.location.href.includes("app=1") ? 1.2 : 0.6
class ScrollAnimation extends Component {
    state = {
        slideWall: 0,
    }
    componentDidMount() {
        this.onLoadScroll()
        window.addEventListener('resize', () => {
            this.onScrollWPosts();
        });
    }
    onLoadScroll = () => {
        if (!isSocialEmbed) {
            const { wall } = this.props;
            window.clearData = this.clearData;
            const prentId = document.querySelector(".tb_app_container");
            prentId?.addEventListener("wheel", e => e.preventDefault(), { passive: false });
            setTimescroll = setTimeout(() => this.scrollDiv_init(wall.ThemeRule.themeId), 500);
        }
    }
    scrollDiv_init = (themeId) => {
        const { slideWall } = this.state
        const { parentId } = this.props
        if (!slideWall) {
            DivElmnt = document.getElementById(parentId);
            if (themeId == 63) ReachedMaxScroll = false;
            (DivElmnt || {}).scrollTop = 0;
            PreviousScrollTop = 0;
            this.onScrollData();
            this.onScrollWPosts();
        }
    };

    onScrollData = () => {
        const { wall } = this.props;
        const delay = wall.Personalization.scrollDelay;
        if (delay) {
            const speed = speedUpPixel(delay);
            ScrollInterval = setInterval(this.scrollDiv, speed);
        }
    };



    onScrollWPosts = () => {
        const { themeHeight, wall, renderID, parentId } = this.props;
        const DivElmnt = document.getElementById(parentId);
        const dataPost = document.getElementById(renderID);
        var themeContainer = document.querySelector(".tb_theme_container");

        if (themeContainer) {
            themeContainer = window.getComputedStyle(themeContainer).getPropertyValue('height');
            if (dataPost) {
                if (window.innerHeight < dataPost.clientHeight) {
                    DivElmnt.style = `height:${themeContainer}`
                    dataPost.style = `position: relative; margin-bottom: ${window.innerHeight}px; min-height: ${dataPost.clientHeight}px`;

                } else {
                    const height = themeHeight && isSocialWall ? themeHeight + (window.editor ? 6 : 0) : 0;
                    DivElmnt.style = `height:${themeContainer}`;
                    DivElmnt.setAttribute("style", `height:${themeContainer}`);
                }
            }
        }

    };


    scrollDiv = () => {
        const { themeId } = this.props.wall.ThemeRule;
        if (ScrollInterval) clearInterval(ScrollInterval);

        const div = DivElmnt || {};

        if (!ReachedMaxScroll) {
            PreviousScrollTop = PreviousScrollTop + scrollSpeedValue
            div.scrollTop = PreviousScrollTop;
            ReachedMaxScroll = div.scrollTop >= div.scrollHeight - div.offsetHeight - 700;
        } else if (themeId === 63) {
            PreviousScrollTop = PreviousScrollTop - scrollSpeedValue
            div.scrollTop = PreviousScrollTop;
            ReachedMaxScroll = ReachedMaxScroll && div.scrollTop === 0 ? false : true;
        } else {
            div.scrollTop = PreviousScrollTop = 0;
            ReachedMaxScroll = false;
        }

        this.onScrollData();
    };


    pauseDiv = () => {
        clearInterval(ScrollInterval);
        clearTimeout(scrollTime);
        scrollTime = setTimeout(this.resumeDiv, 5000);
    };


    resumeDiv = () => {
        const { wall, renderID } = this.props;
        DivElmnt = document.getElementById(renderID);
        PreviousScrollTop = DivElmnt?.scrollTop || 0;
        ScrollInterval = setInterval(this.scrollDiv, speedUpPixel(wall.Personalization.scrollDelay));
    };


    componentDidUpdate(prevProps, prevState) {
        const { postData, wall } = prevProps;
        const { ThemeRule } = this.props.wall;

        if (ThemeRule.themeId !== wall.ThemeRule.themeId) {
            ReachedMaxScroll = false;
            PreviousScrollTop = 0;
        }

        if (postData !== this.props.postData) {
            this.clearTime(1);
            this.scrollDiv_init(ThemeRule.themeId);
        }
    }
    clearData = () => this.clearTime()

    clearTime = (status = 0) => {
        this.setState({ slideWall: status ? 0 : 1 });
        clearInterval(ScrollInterval);
        ScrollInterval = 0;
        clearTimeout(scrollTime);
        clearTimeout(setTimescroll);
        ReachedMaxScroll = false;
        PreviousScrollTop = 0;
    };



    componentWillUnmount() {
        this.clearTime()
    }

    render() {
        return <>
            {({ ...this.props.children })}</>
    }
}
export default memo(ScrollAnimation);